import React, { Fragment } from 'react'
import ReactDOM from 'react-dom'
import posed from 'react-pose'
import styled from 'styled-components'

const Content = posed.div({
  closed: { height: 0 },
  open: { height: 'auto' },
})

const StyledAccordian = styled.div`
  display: flex;
  flex-flow: column;
  align-items: center;
  margin: 0;
  padding: 0;
  .plus {
    padding: 5px;
    border: 2px solid white;
    border-radius: 50%;
  }
  #root {
    display: flex;
    flex-direction: column;
    width: 400px;
    color: white;
    font-family: 'PT Sans', Helvetica, sans-serif;
  }

  h1 {
    margin-bottom: 20px;
    color: Black;
    text-align: center;
  }
  h2 {
    margin: 0;
    color: white;
    font-size: 16px;
    width: 50%;
  }

  .title {
    cursor: pointer;
    background-image: linear-gradient(
      -180deg,
      rgba(100, 100, 240, 1) 0%,
      rgba(100, 160, 220, 1) 300px
    );
    padding: 10px 20px;
    border-bottom: 1px solid rgba(100, 100, 240, 1);
  }

  .content {
    overflow: hidden;
    font-size: 18px;
    background: rgba(100, 220, 240, 0.4);
    color: white;
    width: 50%;
  }

  .content-wrapper {
    padding: 20px 20px;
  }
`

const data = [
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `It doesn't matter how much content you put in each accordian. You only have to define one posed component that animates to "auto" and reuse that.`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
  {
    title: 'Frequently Asked Question',
    content: `height: 'auto'`,
  },
]

class Accordian extends React.Component {
  state = { open: false }

  render() {
    const { open } = this.state

    return (
      <StyledAccordian>
        <h1>Frequently Asked Questions</h1>
        {data.map(({ title, content }, i) => (
          <Fragment>
            <h2
              className="title"
              onClick={() => this.setState({ open: open === i ? false : i })}
            >
              <span className="plus">{open === i ? '- ' : '+ '}</span>
              {title}
            </h2>
            <Content className="content" pose={open === i ? 'open' : 'closed'}>
              <div className="content-wrapper">{content}</div>
            </Content>
          </Fragment>
        ))}
      </StyledAccordian>
    )
  }
}

export default Accordian
